.wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-perspective: 500px;
          perspective: 500px;
  padding-top: 80vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  color: #444;
}
.wrapper > div {
  position: absolute;
  width: 50%;
  height: 100%;
  background-color: #ccc;
  -webkit-transition: opacity .5s .5s ease, -webkit-transform 1s ease;
  transition: opacity .5s .5s ease, -webkit-transform 1s ease;
  -o-transition: transform 1s ease, opacity .5s .5s ease;
  transition: transform 1s ease, opacity .5s .5s ease;
  transition: transform 1s ease, opacity .5s .5s ease, -webkit-transform 1s ease;
}
.wrapper > div:nth-child(1) {
  left: 0;
  top: 0;
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}
.wrapper > div:nth-child(2) {
  right: 0;
  top: 0;
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
}
.wrapper > h2,
.wrapper > p {
  position: absolute;
  width: 100%;
  text-align: center;
  color: #444;
  z-index: 2;
  -webkit-transition: all .8s ease;
  -o-transition: all .8s ease;
  transition: all .8s ease;
}
.wrapper > h2 {
  top: 30vh;
}
.wrapper > p {
  top: 80vh;
  position: absolute;
}
.wrapper.loadingComplete * {
  opacity: 0;
}
.wrapper.loadingComplete > div:nth-child(1) {
  -webkit-transform: rotateY(60deg);
          transform: rotateY(60deg);
}
.wrapper.loadingComplete > div:nth-child(2) {
  -webkit-transform: rotateY(-60deg);
          transform: rotateY(-60deg);
}



