.wrapper {
  width: 100%;
  height: 100%;
  padding: 3em;
  padding-top: 10vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #eee;
}
.wrapper > * {
  text-align: center;
  color: #666;
}
.wrapper a:visited,
.wrapper a {
  color: #222;
}
.wrapper > hr {
  border-color: rgba(100, 100, 100, 0.8);
  border-top-width: 0px;
  border-left-width: 0px;
  border-right-width: 0px;
  -webkit-box-shadow: 0px 0px 2px rgba(100, 100, 100, 0.8), 0px 0px 5px rgba(100, 100, 100, 0.4);
          box-shadow: 0px 0px 2px rgba(100, 100, 100, 0.8), 0px 0px 5px rgba(100, 100, 100, 0.4);
}



