.wrapper {
  width: 100%;
  height: 100%;
  background-color: #eee;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 8vh;
}
.wrapper table,
.wrapper thead,
.wrapper tbody {
  border-collapse: collapse;
  width: 100%;
  background-color: #fff;
}
.wrapper table td,
.wrapper thead td,
.wrapper tbody td {
  border: 1px solid #ddd;
  padding: 5px;
  text-align: center;
  color: #444;
}
.wrapper table td > button,
.wrapper thead td > button,
.wrapper tbody td > button {
  color: #444;
  background-color: inherit;
}
.wrapper tbody tr:nth-of-type(odd) {
  background-color: #f2f2f2;
}
.wrapper .addBtn {
  text-align: right;
}



